import React, { useState, useEffect, useRef, } from 'react';
import Search from './components/citySearchBar';
import Days from './components/daysList';
import useFetch from './useFetch';
import { ErrorBoundary } from 'react-error-boundary'
import cities from 'cities.json'



const App = React.memo(props => {

  const Api_key = '3620601107f4d9544bfa64bd1c29b118';
  const [search, setsearch] = useState('')
  const [query, setquery] = useState('')
  const [geoErr, setgeoErr] = useState('')
  const [autocity, setautocity] = useState([])
  const [lat, setlat] = useState()
  const [lon, setlon] = useState()
  const [located, setlocated] = useState(true)
  const childRef = useRef()


  let { data: data1, err: err1, isLoading: isLoading1 } = useFetch(`https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&appid=${Api_key}`)
  let { data: data2, err: err2, isLoading: isLoading2 } = useFetch(`https://api.openweathermap.org/data/2.5/forecast?q=${query}&appid=${Api_key}`)


  useEffect(() => {
    document.documentElement.className = 'theme-light'
    getLocation()
  }, [])


  const chekafterFetch = (data, err, isLoading) => {
    if (err) return // <div>Error Detected : </div>;
    if (!data && isLoading) return <div>
      <div className="continuous-4"></div>
      <h5>Loading . . .</h5>
    </div>

    if (!data) return null

    return (<ErrorBoundary fallback={<div><h1>Error Detected</h1></div>}>
      <Days daysWeather={data.data.list} country={data.data.city} />
    </ErrorBoundary>
    )
  }



  //////////////////////////////////////
  //// search bar functions 

  function findCity(e) {
    e.preventDefault()
    setquery(search)
    setsearch('')
    e.target.reset()
    setautocity([])
    childRef.current.value = ''
    setlocated(false)
  }


  const citySearch = (e) => {
    setsearch(e.target.value)
    setautocity(autoName(e.target.value) || [])
  }


  function autoName(e) {
    let match;
    try {
      match = cities.filter(country => {
        const regex = new RegExp(`^${e}`, 'gi')
        return country.name.match(regex) || country.country.match(regex);
      })
    } catch {
      alert('Enter country name correctly or not found')
    }
    if (e.length === 0) {
      match = [];
    }
    return match;
  }




  ////////////////////////////////////////////
  // get location and set query

  const getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(ShowPosition, showError);
    } else {
      setgeoErr("Geolocation is not supported by this browser.");
    }
  }

  

  function ShowPosition(position) {
    if (lat === position.coords.latitude.toString() &&
      lon === position.coords.longitude.toString()) {
        setlat((lat)=>lat='')
        setlon((lon)=>lon='')
    }
    setlat(position.coords.latitude.toString())
    setlon(position.coords.longitude.toString())
    setlocated(true)

  }



  function showError(error) {
    switch (error.code) {
      case error.PERMISSION_DENIED:
        setgeoErr("User denied the request for Geolocation.")
        break;
      case error.POSITION_UNAVAILABLE:
        setgeoErr("Location information is unavailable.")
        break;
      case error.TIMEOUT:
        setgeoErr("The request to get user location timed out.")
        break;
      case error.UNKNOWN_ERROR:
        setgeoErr("An unknown error occurred.")
        break;
    }
  }

  ///////////////////////////////////////////////////

  function darkmode() {
    const date = new Date()
    const hour = date.getHours()
    if (hour >= 20 || hour <= 8) {
      document.documentElement.className = 'theme-dark'
    }
    else {
      document.documentElement.className = 'theme-light'
    }
  }



  return (
    <>
      <header>
        <h1>Weathery</h1>
        <a className="copyright" target="_blank" href='https://github.com/mahmoud-irshaid'><img src='github.png' /></a>
      </header>
      <div className='main'>
        <h2>search by country</h2>
        <Search findCity={findCity} citySearch={citySearch} getLocation={getLocation} forwardedRef={childRef} />
        <div className='hints'>
          {
            autocity.map(hint => (<div onClick={(e) => {
              setautocity([])
              setsearch('')
              childRef.current.value = ''
              setlocated(false)
              setquery(e.target.id);
            }}>
              <p id={hint.name}>{hint.country},{hint.name}</p></div>))

          }</div>
        {located ? chekafterFetch(data1, err1, isLoading1) : chekafterFetch(data2, err2, isLoading2)}
        {geoErr}
        {darkmode()}
      </div>

      <a className="copyright" target="_blank" href='https://github.com/mahmoud-irshaid'>© 2021 Copyright by Mahmoud Irshaid</a>
    </>
  );

})
export default App;