import { useState, useEffect } from 'react';
import axios from 'axios';

const useFetch = (url) => {

  const [data, setdata] = useState(null)
  const [err, seterr] = useState(null)
  const [isLoading, setisLoading] = useState(null)

  useEffect(() => {
    setisLoading(true)

    if (!url) {
      setisLoading(true)
      seterr(null)
      setdata(null)
    }
    try {
      axios.get(url)
        .then(data => {
          setisLoading(false)
          seterr(null)
          if (data != null) {
            let copy = JSON.parse(JSON.stringify(data))
            copy.data.list.map(copy => {
              copy.main.temp = Math.floor((copy.main.temp - 273.15) * 9 / 5 + 32)
              copy.main.temp_max = Math.floor((copy.main.temp_max - 273.15) * 9 / 5 + 32)
              copy.main.temp_min = Math.floor((copy.main.temp_min - 273.15) * 9 / 5 + 32)
              return copy
            })
            setdata(copy)
          }
        })
        .catch((error) => {
          setisLoading(false)
          seterr(error)
          // Code for handling the error
        })
    } catch (error) {
      setisLoading(false)
      seterr(error)
    }
  }, [url])


  return ({data, err, isLoading});
}
export default useFetch;
