import S01d from '../animated/01d.svg?sanitize=true'
import S01n from '../animated/01n.svg?sanitize=true'
import S02d from '../animated/02d.svg?sanitize=true'
import S02n from '../animated/02n.svg?sanitize=true'
import S03d from '../animated/03d.svg?sanitize=true'
import S03n from '../animated/03n.svg?sanitize=true'
import S04d from '../animated/04d.svg?sanitize=true'
import S04n from '../animated/04n.svg?sanitize=true'
import S09d from '../animated/09d.svg?sanitize=true'
import S09n from '../animated/09n.svg?sanitize=true'
import S10d from '../animated/10d.svg?sanitize=true'
import S10n from '../animated/10n.svg?sanitize=true'
import S11d from '../animated/11d.svg?sanitize=true'
import S11n from '../animated/11n.svg?sanitize=true'
import S13d from '../animated/13d.svg?sanitize=true'
import S13n from '../animated/13n.svg?sanitize=true'
import S50d from '../animated/50d.svg?sanitize=true'
import S50n from '../animated/50n.svg?sanitize=true'
import React, { Component } from 'react';
class Day extends Component {
    state = { sub:false }
    render() { 
        const svgState = {
            S01d: S01d,
            S01n: S01n,
            S02d: S02d,
            S02n: S02n,
            S03d: S03d,
            S03n: S03n,
            S04d: S04d,
            S04n: S04n,
            S09d: S09d,
            S09n: S09n,
            S10d: S10d,
            S10n: S10n,
            S11d: S11d,
            S11n: S11n,
            S13d: S13d,
            S13n: S13n,
            S50d: S50d,
            S50n: S50n
        }

    return (
        <>
            <div className='dayz' onClick={()=>(this.setState({sub:!this.state.sub}))}>
                <p>{this.props.details[0].dt_txt.slice(5, 10)}</p>
                <img src={svgState['S' + this.props.details[Math.floor((this.props.details.length - 1) / 2)].weather[0].icon]} />
                <p>{this.props.details[Math.floor((this.props.details.length - 1) / 2)].main.temp}&deg; / {this.props.details[0].main.temp}&deg;</p>
            </div>
            <div className='subdayz'>
                {this.state.sub && this.props.details.map(hour => {
                    return (
                        <div className='dayCard' key={hour.dt}>
                            <img src={svgState['S' + hour.weather[0].icon]} />

                            <div className='info'>
                                <div>
                                    <h3>{hour.main.temp}&deg;</h3>
                                    <p>{hour.dt_txt.slice(10, 16)}</p>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </>
    )
            }

}
export default Day;